@import '../../style/variables';

.signInDiv {
    display: flex;
    width: 70%;
    flex-direction: column;
    justify-content: end;
}

  .signInForm {
    background-color: $lightAccent;
    padding: 30px 30px 30px 30px;
    border-radius: 10px;
    top: 50%;
    left: 50%;
    width: 400px;
    box-shadow: 5px 10px 10px rgba($dark, 0.2);
      
  

   > input {
    font-family: $monoFont;
    display: block;
    border-radius: 5px;
    font-size: 16px;
    background: $light;
    width: 100%;
    border: 0;
    padding: 10px 10px;
    margin: 15px -10px;
  
  }
}

  .loginButton {
    font-family: $mainFont;
    cursor: pointer;
    color: #fff;
    background: rgba(255, 255, 255, 0.301);
    font-size: 16px;
    text-transform: uppercase;
    width: 100%;
    text-align: center;
    border: 0;
    padding: 10px 0;
    border-radius: 5px;
  
  
    
    &:hover {
      background-color: darken($mainColor, 5%);
    }
  }
  
