@import '../../style/variables';

#nav {
    background: $mainColor;

    ul {
      list-style-type: none;
      margin: 0;
      padding: 0;

      li {
        display: inline-block;
        padding: 0.5rem 2rem;
        position: relative;

        span {
          color: #111;
          text-decoration: none;

          &:after {
            bottom: 0;
            content: "";
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
          }

          &.here,
          &.here:hover {
            color: red;
          }
        }

        &:hover {
          background: rgb(255, 255, 255);
          }
        }
      }
    }
