#formDiv{display:flex;flex-direction:row;justify-content:flex-start;margin:2em;background-color:#D14344;width:50%}#formDiv h3{font-family:"Fredoka One",cursive;font-size:30px}#formDiv .jobForm{font-family:"Varela Round",sans-serif;display:flex;justify-content:flex-start;margin:2em;flex-direction:column}#formDiv .jobForm label{display:flex;flex-direction:column;width:175px;font-size:20px;margin-bottom:1.5em;color:white;background-color:rgba(90,90,90,0.308)}

.myjob-table{display:flex;flex-direction:row;justify-content:flex-start;margin:2em;background-color:#D14344;width:50%}.myjob-table h3{font-family:"Fredoka One",cursive;font-size:30px}

.dashBoardDiv{display:flex;flex-direction:row}


header{background:#343445}header h1{font-family:"Fredoka One",cursive;font-size:45px;color:#eee;display:inline-block;margin:1em auto}

.modal{bottom:0;left:0;position:absolute;right:0;top:0;z-index:100}.modal:after{background:#000;bottom:0;content:' ';left:0;opacity:.5;position:absolute;right:0;top:0}.modal>div{background:#fff;box-shadow:0 1px 4px rgba(0,0,0,0.3),0 0 40px rgba(0,0,0,0.1) inset;box-sizing:border-box;border-radius:5px;display:flex;flex-direction:column;height:20%;margin:auto;padding:0;position:relative;top:50%;transform:translateY(-50%);width:35%;z-index:1000}.modal>div>header{background:#ccc;border-bottom:0 none;border-top-left-radius:5px;border-top-right-radius:5px;box-shadow:0 1px 5px rgba(0,0,0,0.46);display:flex;font-size:1em;font-weight:bold;height:1.5em;line-height:1.5em;padding:.25em 1em}.modal>div>header span{color:#000;flex:1 1}.modal>div>header button{background:inherit;border:0;cursor:pointer;order:1;text-align:center}.modal>div>header button:hover{background:#000;color:#fff}.modal>div>div{flex:1 1;overflow:auto;padding:25px}

.signInDiv{display:flex;width:70%;flex-direction:column;justify-content:end}.signInForm{background-color:#91B0BB;padding:30px 30px 30px 30px;border-radius:10px;top:50%;left:50%;width:400px;box-shadow:5px 10px 10px rgba(52,52,69,0.2)}.signInForm>input{font-family:"PT Mono",monospace;display:block;border-radius:5px;font-size:16px;background:#F9F9F9;width:100%;border:0;padding:10px 10px;margin:15px -10px}.loginButton{font-family:"Varela Round",sans-serif;cursor:pointer;color:#fff;background:rgba(255,255,255,0.301);font-size:16px;text-transform:uppercase;width:100%;text-align:center;border:0;padding:10px 0;border-radius:5px}.loginButton:hover{background-color:#f47720}

#nav{background:#F58638}#nav ul{list-style-type:none;margin:0;padding:0}#nav ul li{display:inline-block;padding:0.5rem 2rem;position:relative}#nav ul li span{color:#111;text-decoration:none}#nav ul li span:after{bottom:0;content:"";left:0;position:absolute;right:0;top:0}#nav ul li span.here,#nav ul li span.here:hover{color:red}#nav ul li:hover{background:#fff}

h1{font-family:"Varela Round",sans-serif}.splashDiv{width:100%;display:flex;justify-content:center;flex-direction:column}

